a {
	text-decoration: none;
}

.header-main {
	margin-top: 2rem;
	padding-bottom: 2rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #e7e7e7;
}

.header-main h4 {
	margin: 0;
	margin-right: 2rem;
	flex-grow: 1;
	text-transform: uppercase;
	font-size: 2rem;
	letter-spacing: 1px;
}

.header-main nav {
	display: flex;
	justify-content: center;
	flex-grow: 1;
}

section {
	margin-top: 3rem;
}

.nav {
	width: 100%;
	list-style-type: none;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0;
}

.nav-link {
	display: inline-block;
	margin: 0;
}

.nav-button {
	padding: 0;
	border: none;
	margin: 0;
	height: fit-content;
	line-height: normal;
}

.nav-button:hover {
	color: black;
}

/* TODO: hover etc för länkar*/

.board-preview-list {
	list-style-type: none;
}

.button-row button:first-child {
	margin-right: 2rem;
}

.placard {
	background-color: #e7e7e7;
	padding: 2rem;
	margin-top: 3rem;
}

.placard-actions-list {
	list-style-type: none;
	display: flex;
	justify-content: flex-start;
	margin: 0;
}

.placard-action-list-item {
	display: inline-block;
	margin-right: 2rem;
}

.placard-input-title,
.board-input-title,
.board-input-member,
.access-input {
	width: 100%;
	max-width: 500px;
	margin-right: 2rem;
}

.placard-input-text {
	width: 100%;
	max-width: 500px;
	resize: vertical;
	height: 150px;
	max-height: 300px;
}

.members-list {
	list-style-type: none;
	width: 100%;
	max-width: 500px;
}

.members-list button {
	margin: 0;
}

.members-list li {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1rem 0;
	margin: 0;
	border-bottom: 1px solid rgb(219, 219, 219);
}

.members-list li:last-child {
	border: none;
}

.italics {
	font-style: italic;
}
